@import "../../common/styles/sass/mixins";

.nav {
   .list {

      display: flex;
      flex-direction: row;
      align-items: center;

      column-gap: 1.5rem;

      .item {
         .link {
            display: inline-block;
            position: relative;
            font-weight: 700;
            @include adaptive-font(24, 16);
            color: var(--color-text);
            text-decoration: none;
            transition: width 0.2s ease;

            &:after {
               content: '';
               @include position(absolute, initial, 0, -3px, 0);
               width: 0;
               height: 2px;
               border-right: 5px;
               background-color: var(--color-text);;
               transition: width 0.3s ease;
            }

            &:hover::after {
               width: 100%;
            }
         }
      }
   }

}
